@import url(https://fonts.googleapis.com/css?family=Montserrat);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  line-height: 1.4;
  color: #4caf50;
}
a{
  color: #e91e63;
}
.container{
  min-height: 100vh;
  display: grid;
  grid-template-columns:1fr 1fr;
  text-align: center;
}

.editor-container{
  border-right: 2px solid #132131;
}
.preview-container{
  margin-bottom:3em;
}

textarea,#preview{
  width: 60%;
  max-width: 35em;
  margin: auto;
  padding: 1.5em;
  overflow: auto;
  word-wrap: break-word;
  min-height: 500px;
  background: #132131;
  color: #fff;
  padding: 1em 1.5em;
  font-size: 1em;
  text-align: left;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.9);
}
h1:first-child:not(:last-child){
  margin: 0.5em auto;
}
#preview > *{
  margin-bottom: 0.5em;
}
textarea{
  border: 0;
  resize: none;
  outline:none;
  max-height: 500px;

}
#preview img{
  width: 50px;
  height: 50px;  
}

@media (max-width:50em){
  .container{
    grid-template-columns: 1fr;
  }
  .editor-container{
    border:0;
  }
}
@media (max-width:40em){
  textarea,#preview {
    width: 80%;
  }
}
